<template>
      <v-sheet
        rounded="lg"
        class="w-border"
      >
      <v-skeleton-loader
        class="mx-auto"
        type="card"
        v-if="!isLoaded"
      ></v-skeleton-loader>
      <v-card class="w-border" v-if="isLoaded">
        <v-card-title class="title">
          <span class="card-title">Events & Holidays</span>
          <v-spacer></v-spacer>
          <v-btn
            outlined
            @click="dialog = true"
          >
            Add Event
          </v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
         <the-calendar></the-calendar>
        </v-card-text>
      </v-card>
               
      <v-dialog
            v-model="dialog"
            persistent
            max-width="500px"
            max-height="400px"
            class="w-border"
          >
          <v-card>
            <v-card-title>
              <v-container>
                <v-row>
                  <v-col></v-col>
                  <v-col class="d-flex justify-center">
                    <span v-if="! isUpdateEvent" class="dialog-name">New Event</span>
                    <span v-if="isUpdateEvent" class="dialog-name">Event</span>
                  </v-col>
                  <v-col>
                    <v-btn 
                      icon
                      class="float-right"
                      @click="close"
                     >
                     <v-icon>mdi-close</v-icon>
                    </v-btn> 
                  </v-col>
                </v-row>
              </v-container>           
             </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="6">
                      <v-text-field
                        label="Name"
                        required  
                        v-model="eventName"
                        hide-details="true"
                        solo
                      ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-checkbox
                            label="All day event"
                            hide-details="true"
                            v-model="allTimeEvent"
                            @change="allTimeEventChanged"
                        >
                        </v-checkbox>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-menu
                          ref="menu1"
                          v-model="menu1"
                          :close-on-content-click="false"
                          transition="scale-transition"
                          max-width="290px"
                          min-width="auto"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="dateFormatted"
                              label="Start Time"
                              hint="MM/DD/YYYY 00:00 format"
                              persistent-hint
                              prepend-icon="mdi-calendar"
                              @click = "menu1 = true"
                              v-on="on"
                              v-bind="attrs"
                              solo
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            v-if="isDate"
                            v-model="date"
                            no-title
                            @input="closeDatePicker"
                          ></v-date-picker>
                          <v-time-picker
                            v-if="!isDate"
                            v-model="timeStep1"
                            class="mt-4"
                            format="24hr"
                            no-title
                            @click:minute="closeTimePicker('menu1')"
                          ></v-time-picker>
                        </v-menu>
                    </v-col>
                    <v-col>
                        <v-menu
                            ref="menu2"
                            v-model="menu2"
                            :close-on-content-click="false"
                            transition="scale-transition"
                            max-width="290px"
                            min-width="auto"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="date2Formatted"
                              label="End Time"
                              hint="MM/DD/YYYY 00:00 format"
                              persistent-hint
                              prepend-icon="mdi-calendar"
                              @click = "menu2 = true"
                              v-on="on"
                              v-bind="attrs"
                              solo
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            v-if="isDate"
                            v-model="date2"
                            no-title
                            @input="closeDatePicker"
                          ></v-date-picker>
                          <v-time-picker
                            v-if="!isDate"
                            v-model="timeStep2"
                            class="mt-4"
                            format="24hr"
                            no-title
                            @click:minute="closeTimePicker('menu2')"
                          ></v-time-picker>
                        </v-menu>
                    </v-col>
                </v-row>
                <v-row>
                  <v-col cols="6">
                    <v-text-field
                      label="Location"
                      required  
                      v-model="location"
                      hide-details="true"
                      v-if="isUpdateEvent"
                      solo
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>
                    <v-textarea
                      label="Description"
                      required  
                      v-model="description"
                      hide-details="true"
                      v-if="isUpdateEvent"
                      rows="3"
                      solo
                    >
                    </v-textarea>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="12">
                    <v-btn v-if="!isUpdateEvent" text @click="createNewEvent">Create</v-btn>
                    <v-btn v-if="isUpdateEvent" text @click="updateCurrentEvent('update')">Refresh</v-btn>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
          </v-card>
      </v-dialog>
      </v-sheet>
</template>

<script>
import moment from 'moment';
import { mapGetters, mapActions } from 'vuex';
import TheCalendar from '../components/TheCalendar.vue';
import Vue from 'vue';
 export default {
    components: { TheCalendar },
    data: ()=> ({
        dialog: false,
        eventName: '',
        location: '',
        description: '',
        dialogError: false,
        date: new Date().toISOString().substr(0, 10),
        date2: new Date().toISOString().substr(0, 10),
        dateFormatted: moment().format('DD/MM/YYYY, hh:mm'),
        date2Formatted: moment().format('DD/MM/YYYY, hh:mm'),
        menu1: false,
        menu2: false,
        isDate: true,
        timeStep1: '00:00',
        timeStep2: '00:00',
        allTimeEvent: false,
        isUpdateEvent: false,
    }),
    methods: {
      ...mapActions('calendar', [
        'addEvent',
        'updateEvent'
      ]),
      createNewEvent: async function(){
          
          let newEvent = {
            title: '',
            allDayEvent: false,
            startDate: '',
            endDate: ''
          }
          newEvent.title = this.eventName;
          newEvent.allDayEvent = this.allTimeEvent;

          let hour1 = this.timeStep1.split(':')[0];
          let hour2 = this.timeStep2.split(':')[0];
          let ampm1 = 'AM';
          let ampm2 = 'AM';
          if(hour1 > 12) ampm1 = 'PM'
          if(hour2 > 12) ampm2 = 'PM'
          newEvent.startDate = moment(this.date).format('MM/DD/YYYY') + ' ' + this.timeStep1 + ' ' + ampm1;
          newEvent.endDate = moment(this.date2).format('MM/DD/YYYY') + ' ' + this.timeStep2 + ' ' + ampm2;
          
          this.addEvent(newEvent);
          await this.$root.$refs.TheCalendar.loadEvents();
          this.close();
      },
      deleteCurrentInstruction: async function(id){},
      editCurrentInstruction: async function(id){},
      close: function(){
            this.eventName = '';
            this.dialog = false;
            this.location = '';
            this.description = '';
            this.isUpdateEvent = false;
            this.id = '';
      },
      closeDatePicker(){
            this.isDate = false;
      },
      closeTimePicker(type){
            this.isDate = true;
            if(type == 'menu1'){
                this.dateFormatted = moment(this.date).format('DD/MM/YYYY') + ', ' + this.timeStep1;
                this.menu1 = false;
            }else{
                this.date2Formatted = moment(this.date2).format('DD/MM/YYYY') + ', ' + this.timeStep2;
                this.menu2 = false;
            }
      },
      allTimeEventChanged(){
      },
      changeCurrentEvent: function(event){
          this.eventName = event.TITLE;
          this.location = JSON.parse(event.EXTENDEDDATA).location;
          this.description = event.DETAILS != '?' ? event.DETAILS : '' ;
          this.isUpdateEvent = true;
          this.dialog = true;
          this.id = event.ID;
      },
      newEvent: function(){
          this.isUpdateEvent = false;
          this.dialog = true;
      },
      updateCurrentEvent: async function(status, obj){
           let updateEvt = {
            id: '',
            title: '',
            allDayEvent: false,
            startDate: '',
            endDate: '',
            location: '',
            details: ''
          }
          updateEvt.id = this.id;
          updateEvt.title = this.eventName;
          updateEvt.allDayEvent = this.allTimeEvent;
          updateEvt.location = this.location;
          updateEvt.details = this.description;

          let hour1 = this.timeStep1.split(':')[0];
          let hour2 = this.timeStep2.split(':')[0];
          let ampm1 = 'AM';
          let ampm2 = 'AM';
          if(hour1 > 12) ampm1 = 'PM'
          if(hour2 > 12) ampm2 = 'PM'
          updateEvt.startDate = moment(this.date).format('MM/DD/YYYY') + ' ' + this.timeStep1 + ' ' + ampm1;
          updateEvt.endDate = moment(this.date2).format('MM/DD/YYYY') + ' ' + this.timeStep2 + ' ' + ampm2;
          if(status == 'delete'){
            updateEvt.isDelete = true;
          }else{
            updateEvt.isDelete = false;
          }
          if(obj){
            updateEvt.id = obj.ID;
            updateEvt.title = obj.TITLE;
          }
          await this.updateEvent(updateEvt);
          this.close();
      }
    },
    computed: {
        ...mapGetters(['isLoaded'])
    }
 }
</script>

<style lang="scss" scoped>
  .title{
    background: linear-gradient(0deg, rgba(194,219,231,1) 40%, rgba(220,236,248,1) 90%)
  }
  .w-border{
    border: 0px solid black;
    background-color: #eee;
  }
</style>
