<template>
    <v-sheet
      rounded="lg"
      class="w-primary"
      min-height="85vh"
    >
      <v-container>
        <v-row>
          <v-col>
            <v-card
              class="w-secondary"
              min-height="85vh"
            >
            <v-card-title class="w-text  w-primary">
                <v-icon>{{commonNewsIcon}}</v-icon> <span class="name">{{nameOfComponent}}</span>
            </v-card-title>
              <v-virtual-scroll
                v-if="items.length > 0"
                :items="items"
                max-height="85vh"
                item-height="50"
              >
                <template v-slot:default="{ item }">
                  <v-list-item @click="openDialog(item.NAME, item.EXTENDEDDATA)" :key="item.DATAID">    
                    <v-list-item-content>
                      <v-list-item-title>
                       <strong>{{ item.NAME }}</strong>
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider></v-divider> 
                </template>
              </v-virtual-scroll>
            </v-card>
          </v-col>
        </v-row>
      </v-container> 
      <v-dialog
         v-model="dialog"
         fullscreen
         hide-overlay
         transition="dialog-bottom-transition"
       >
         <template v-slot:activator="{ on, attrs }">
             <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
         </template>
          <v-card>
             <v-toolbar
               class="title"
             >
               <v-btn
                 icon
                 @click="dialog = false"
               >
                 <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>{{dialogTitle}}</v-toolbar-title>
             </v-toolbar>  
             <v-card-text class="dialog-text">
                <v-container fluid>
                  <div v-html="dialogContent"></div>
                </v-container>
              </v-card-text>                    
           </v-card>
       </v-dialog> 
    </v-sheet>
</template>

<script>
import { mapGetters } from 'vuex';
import Vue from 'vue';
export default {
  name: 'CommonNews',
  data: () => ({ 
    items: [],
    dialog: false,
    dialogContent: '',
    dialogTitle: '',
    nameOfComponent: '',
    commonNewsIcon: window.opentext.commonNewsIcon
  }),
  methods: {
    openDialog: async function(title, data){
      this.dialogContent = '';
      this.dialogTitle = '';
      this.dialogTitle = title;
      this.dialogContent = JSON.parse(data).Story;
      const imageId = JSON.parse(data).ImageID;
      if(imageId){
         const config = {
           headers: { 'OTCSTicket': window.opentext.OTCSTicket }
         }
         let url = `${window.opentext.mainurl}/api/v1/nodes/${imageId}/content`;
         const response = await fetch(url, config);
         const image = await response.blob();
         this.dialogContent += `<img src="${window.URL.createObjectURL(image)}" width="350px">`;
      }
      this.dialog = true;
    }
  },
  created: async function(){
    this.nameOfComponent = this.$route.params.name;
    this.items = await Vue.prototype.$api.general.get(window.opentext.webreportIds.newsId, '&parentId='+ this.$route.params.id);
  }
}
</script>

<style lang="scss" scoped>
  .name{
    margin-left: 10px;
  }
  .title{
    background: linear-gradient(0deg, rgba(194,219,231,1) 40%, rgba(220,236,248,1) 90%)
  }
  .dialog-text{
      color: black !important;
  }
  .w-primary{
    background-color: #eee;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
   .w-text{
    color: black;
  }
</style>
