<template>
    <v-container>
        <v-row>
            <v-col>
                <v-card class="bgColor" flat>
                    <v-card-text>
                        <v-data-table
                         dense
                         :headers = "headers"
                         :items = "items"
                         class="bgColor"
                        >
                          <template v-slot:top>
                            <v-spacer></v-spacer>
                          </template>
                          <template v-slot:item.actions="{ item }">
                            <v-icon
                              small
                              class="mr-2"
                              @click="editItem(item)"
                            >
                              mdi-pencil
                            </v-icon>
                            <v-icon
                              small
                              @click="deleteItem(item)"
                            >
                              mdi-delete
                            </v-icon>
                          </template>
                        </v-data-table>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
          <v-dialog 
            v-model="isVacancy"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition">
            <v-toolbar
                class="title">
              <v-btn
                 icon
                 @click="close()"
               >
                 <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>{{title}}</v-toolbar-title>
               <v-spacer></v-spacer>
               <v-btn outlined @click="save()">Save</v-btn>
            </v-toolbar>
            <v-card color="#f7f8f8">
              <v-card-text>
                <the-news ref="addNewVacancy" :channel="876218"></the-news>
              </v-card-text>
            </v-card>
         </v-dialog>
    </v-container>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import TheNews from '../components/TheNews';
import moment from 'moment';
export default {
    name: 'HrVacancy',
    components: { TheNews },
    props: ['isOpen', 'hrTab'],
    computed: {
        ...mapGetters('hr', [
            'vacancyHeadlines'
        ])
    },
    created () {
      this.initialize()
    },
    watch: {
      isOpen: function(val){
        console.log(this.hrTab);
        if(this.hrTab == 1)
          this.isVacancy = val;
          console.log('Vugar', this.isVacancy);
      }
    },
     data: ()=>({
        isVacancy: false,
        title: 'Vacancies',
        headers: [
          { text: 'Id', value: 'id' },
          {
            text: 'Name',
            align: 'start',
            value: 'name',
            sortable: false
          },
          { text: 'Date', value: 'date', sortable: false },
          { text: 'Actions', value: 'actions', sortable: false }
        ],
        items: []
    }),
    methods:{
        addVacancy: async function(){
          this.isVacancy = true;
        },
        editItem: function(){

        },
        deleteItem: function(){

        },
         initialize () {
          this.vacancyHeadlines.map(x=>{
              this.items.push({
                  id: x.Id,
                  name: x.Headline,
                  date: moment(x.Date).format('DD/MM/YYYY')
              })
          });
        },
        close: function(){
          this.isVacancy = false; 
          this.$emit('closeDialog');
        },
        save: async function(){
          await this.$refs.addNewVacancy.add();
        }
    }
}
</script>

<style lang="scss" scoped>
  .title{
    background: linear-gradient(0deg, rgba(194,219,231,1) 40%, rgba(220,236,248,1) 90%)
  }
  .bgColor{
    background-color: #f7f8f8;
  }
  .secondBgColor{
    background-color: #eee;
  }
</style>