const userSelect = (formName, fieldPrefix) => {
    let url = window.baseUrl + '?func=user.SelectUserDlg&formname=' + formName + '&fieldprefix=' + fieldPrefix + '&title=Select%20User&DisplayUserName&NoGroups=FALSE';
    let w; 
    url = url + '&NoGroupsSelectable=TRUE'
    w = window.open(url,"","height=340,width=800,scrollbars=yes,resizable=yes,menubar=no,toolbar=yes,status=yes");
    
    if ( w.focus )
    {
    w.focus();
    }
    return w;
}

export default userSelect;