const signalR = require("@microsoft/signalr");

export default {
    install(Vue, options){
        console.log(signalR);
        Vue.prototype.$signalRServerConnection = new signalR.HubConnectionBuilder()
            .withUrl(window.opentext.hubUrl)
            .configureLogging(signalR.LogLevel.Information)
            .build();
        Vue.prototype.$signalRServerConnection.start();
    }
}