<template>
    <div>
        <v-sheet
          height="5vh"
        >      
          <v-toolbar color="#eee" flat>
            <v-btn
            icon
            class="ma-2"
            @click="$refs.calendar.prev()"
          >
            <v-icon>mdi-chevron-left</v-icon>
            </v-btn>
            <v-btn
            icon
            class="ma-2"
            @click="$refs.calendar.next()"
          >
            <v-icon>mdi-chevron-right</v-icon>
            </v-btn>
            <v-toolbar-title class="cMonth" v-if="$refs.calendar">
             {{ $refs.calendar.title }}
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn-toggle v-model="typeX" mandatory>
            <v-btn class="bgColor" text @click="type = 'month'">Month</v-btn>
            <v-btn class="bgColor" text @click="type = 'week'">Week</v-btn>
            <v-btn class="bgColor" text @click="type = 'day'">Day</v-btn>
            </v-btn-toggle>
          </v-toolbar>
        </v-sheet>
        <v-sheet height="50vh" style="margin-top: 50px">
          <v-calendar
            color="primary"
            ref="calendar"
            v-model="value"
            :weekdays="weekday"
            :type="type"
            :events="calendarEvents"
            :event-overlap-mode="mode"
            :event-overlap-threshold="30"
            :event-color="getEventColor"
            @change="loadEvents"
            @click:event="showEvent"
            @click:more="viewDay"
            @click:date="viewDay"
          ></v-calendar>
          <v-menu
            v-model="selectedOpen"
            :close-on-content-click="false"
            :activator="selectedElement"
            offset-x
          >
            <v-card
              color="grey lighten-4"
              min-width="350px"
              flat
            >
              <v-toolbar
                :color="selectedEvent.color"
                dark
              >
                <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <span v-html="selectedEvent.details"></span>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  text
                  color="secondary"
                  @click="selectedOpen = false"
                >
                  Cancel
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-menu>
        </v-sheet>
    </div>
</template>

<script>
import moment from 'moment';
import { mapGetters, mapActions } from 'vuex';
export default {
    data: () => ({
        currentMonth: moment().format('MMMM'),
        currentYear: new Date().getFullYear(),
        typeX: 0,
        eventDialog: false,
        weekdays: [
            { text: 'Mon - Sun', value: [1, 2, 3, 4, 5, 6, 0] },
            { text: 'Mon - Fri', value: [1, 2, 3, 4, 5] },
        ],
        type: 'month',
        types: ['month', 'week', 'day'],
        mode: 'stack',
        modes: ['stack', 'column'],
        weekday: [1, 2, 3, 4, 5, 6, 0],
        value: '',
        calendarEvents: [],
        colors: ['blue', 'indigo', 'deep-purple', 'cyan', 'green', 'orange', 'grey darken-1'],
        names: ['Meeting', 'Holiday', 'PTO', 'Travel', 'Event', 'Birthday', 'Conference', 'Party'],
        selectedEvent: {},
        selectedElement: null,
        selectedOpen: false
    }),
    computed: {
      ...mapGetters('calendar', [
        'events'
      ])
    },
    created: function(){
        this.$root.$refs.TheCalendar = this;
        let that = this;
        document.addEventListener('keydown', function(event){
        	if(event.key === "Escape"){
        		if(that.dialog){
                    that.close();
                }
        	}
        });
    },
    methods: {
      ...mapActions([
        'getApiData'
      ]),
      loadEvents: async function(){
        this.calendarEvents = [];
        await this.getApiData({
            prefix: 'calendar',
            name: 'events',
            webreportId: window.opentext.webreportIds.eventsId,
            params: '&calendarId='+ window.opentext.cicCommunity
        })
        this.events.forEach(event => {
          const isAllDay = JSON.parse(event.EXTENDEDDATA);
          const location = JSON.parse(event.EXTENDEDDATA).location;
          const details = event.DETAILS != '?' ? event.DETAILS : '';
          let name = event.TITLE;
          name += location ? ' ('+location+')' : ''  
          this.calendarEvents.push({
            name: name,
            start: new Date(event.STARTDATE),
            end: new Date(event.ENDDATE),
            color: 'green',
            timed: isAllDay,
            details: details
          })
        });
      },
      viewDay: async function( { date } ){
        this.value = date;
        this.type = 'day';
        this.typeX = 2;
      },
      getEventColor (event) {
        return event.color
      },
      showEvent ({ nativeEvent, event }) {
        const open = () => {
          this.selectedEvent = event
          this.selectedElement = nativeEvent.target
          requestAnimationFrame(() => requestAnimationFrame(() => this.selectedOpen = true))
        }

        if (this.selectedOpen) {
          this.selectedOpen = false
          requestAnimationFrame(() => requestAnimationFrame(() => open()))
        } else {
          open()
        }

        nativeEvent.stopPropagation()
      },
    }
}
</script>

<style lang="scss" scoped>
    .bgColor{
      background-color: #f7f8f8;
    }
    .w-dialog {
      position: fixed;
      top: 0;
      right: 0;
      height: 100vh;
      width: 30vw;
      background-color: white;
    }
    .wclose-btn{
      position: absolute;
      right: 1vw;
    }
    .w-title{
      padding: 10px;
      height: 8vh;
      color: grey;
      background-color: #eee;
    }
    .cMonth{
      text-transform: uppercase;
      font-weight: bold;
      color: grey;
    }
</style>