<template>
    <v-form>
        <v-container>
            <v-row>
                <v-col cols="3">
                    <v-text-field
                        label="Name"
                        required
                        outlined
                        v-model="name"
                        hide-details="false"
                        :disabled="isDisabled"
                    ></v-text-field>
                </v-col>
            </v-row>
            <!-- <v-row>
                <v-col cols="8">
                    <v-text-field
                        label="Başlıq"
                        required
                        outlined
                        v-model="headline"
                        hide-details="false"
                    ></v-text-field>
                </v-col>
            </v-row> -->
            <!-- <v-row>
                <v-col cols="12">
                    <v-textarea
                        label="Əsas məqamlar" 
                        outlined
                        v-model="highlights"
                        hide-details="false"
                        ></v-textarea>
                </v-col>
            </v-row> -->
            <v-row>
                <v-col>
                    <v-card class="bgColor" flat>
                        <v-card-title>Start Date</v-card-title>
                         <v-menu
                            ref="menu1"
                            v-model="menu1"
                            :close-on-content-click="false"
                            transition="scale-transition"
                            max-width="290px"
                            min-width="auto"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="dateFormatted"
                              label="Start Time"
                              hint="MM/DD/YYYY 00:00 format"
                              persistent-hint
                              prepend-icon="mdi-calendar"
                              @click = "menu1 = true"
                              v-on="on"
                              v-bind="attrs"
                              solo
                              :disabled="isDisabled"
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            v-if="isDate"
                            v-model="date"
                            no-title
                            @input="closeDatePicker"
                            :disabled="isDisabled"
                          ></v-date-picker>
                          <v-time-picker
                            v-if="!isDate"
                            v-model="timeStep1"
                            class="mt-4"
                            format="24hr"
                            no-title
                            @click:minute="closeTimePicker('menu1')"
                            :disabled="isDisabled"
                          ></v-time-picker>
                        </v-menu>
                    </v-card>
                </v-col>
                <v-col>
                    <v-card class="bgColor" flat>
                        <v-card-title>End Date</v-card-title>
                        <v-menu
                            ref="menu2"
                            v-model="menu2"
                            :close-on-content-click="false"
                            transition="scale-transition"
                            max-width="290px"
                            min-width="auto"
                        >
                          <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              v-model="date2Formatted"
                              label="End Time"
                              hint="MM/DD/YYYY 00:00 format"
                              persistent-hint
                              prepend-icon="mdi-calendar"
                              @click = "menu2 = true"
                              v-on="on"
                              v-bind="attrs"
                              solo
                              :disabled="isDisabled"
                            ></v-text-field>
                          </template>
                          <v-date-picker
                            v-if="isDate"
                            v-model="date2"
                            no-title
                            @input="closeDatePicker"
                            :disabled="isDisabled"
                          ></v-date-picker>
                          <v-time-picker
                            v-if="!isDate"
                            v-model="timeStep2"
                            class="mt-4"
                            format="24hr"
                            no-title
                            @click:minute="closeTimePicker('menu2')"
                            :disabled="isDisabled"
                          ></v-time-picker>
                        </v-menu>
                    </v-card>
                </v-col>
            </v-row>
            <v-row>
                <v-col>
                    <p><u>Main Story</u></p>
                    <the-rich-text-editor 
                        :id="'thestory'"
                        :name="'thestory'"
                        :rows="5"
                        :cols="40"
                    ></the-rich-text-editor>
                </v-col>
            </v-row>
            <!-- <v-row>
                <v-col>
                    <v-file-input
                      label="File əlavə etmək"
                      outlined
                    ></v-file-input>
                </v-col>
            </v-row> -->
            <!-- <v-row>
                <v-col class="d-flex justify-center">
                    <v-btn text @click="add" :disabled="isDisabled">Əlavə et</v-btn>
                </v-col>
            </v-row> -->
        </v-container>
    </v-form> 
</template>

<script>
import TheRichTextEditor from './TheRichTextEditor.vue'
import moment from 'moment';
import { mapActions } from 'vuex';
export default {
  components: { TheRichTextEditor },
  name: 'TheNews',
  props: ['channel', 'name', 'story', 'date', 'date2', 'isDisabled'],
  event: ['close'],
  data: vm => ({
      isDate: true,
      menu1: false,
      menu2: false,
      dateFormatted: moment().format('DD/MM/YYYY, hh:mm'),
      date2Formatted: moment().format('DD/MM/YYYY, hh:mm'),
      timeStep1: '00:00',
      timeStep2: '00:00'
  }),
  watch: {
      story: function(val) {
        CKEDITOR.instances['thestory'].setData(val);
      },
      isDisabled: function(val){
        CKEDITOR.instances['thestory'].setReadOnly(val);
      }
  },
  methods: {
    ...mapActions('admin', [
          'createNews'
      ]),
    closeDatePicker(){
        this.isDate = false;
    },
     closeTimePicker(type){
        this.isDate = true;
        if(type == 'menu1'){
            this.dateFormatted = moment(this.date).format('DD/MM/YYYY') + ', ' + this.timeStep1;
            this.menu1 = false;
        }else{
            this.date2Formatted = moment(this.date2).format('DD/MM/YYYY') + ', ' + this.timeStep2;
            this.menu2 = false;
        }
    },
    add(){
        const efDate = moment(moment(this.date).toDate()).format(`YYYY-MM-DDT${this.timeStep1}:00`);
        const expDate = moment(moment(this.date2).toDate()).format(`YYYY-MM-DDT${this.timeStep2}:00`);
        const obj = {
            channelId: this.channel, //'876218',
            name: this.name,
            effective_date: efDate,
            expiration_date: expDate,
            // headline: this.headline,
            // highlights: this.highlights,
            story: CKEDITOR.instances['thestory'].getData()
        };
        this.createNews(obj);
        this.$emit('close');
    }   
  }
}
</script>

<style lang="scss" scoped>
   .row + .row {
        margin-top: 0px;
    }
    .bgColor{
        background-color: #f7f8f8;
    }
</style>