import axios from 'axios';
import Vue from 'vue';

export default {
    namespaced: true,
    state: () => ({
      blogHeadlines: [],
      vacancyHeadlines: [],
      vacancies: [],
      news: [],
      motivationStories: []
    }),
    getters: {
      blogHeadlines: (state) => state.blogHeadlines,
      vacancyHeadlines: (state) => state.vacancyHeadlines,
      vacancies: (state) => state.vacancies,
      news: (state) => state.news,
      motivationStories: (state) => state.motivationStories
    },
    mutations: {
      setVacancyHeadlines: (state, payload) => {
          state.vacancyHeadlines = JSON.parse(JSON.stringify(payload));
      }
    },
    actions: {
        createBlog: async(context, payload) => {
            const id = await getWebCacheId();
            const details = {
                        'func': 'll',
                        'objType': '357',
                        'objAction': 'wizard2',
                        'parentId': '-' + window.opentext.hrBlog,
                        'nextURL': window.opentext.nextUrl,
                        "stepID": '1',
                        "cacheID": id,
                        "secureRequestToken": window.opentext.secureToken,
                        'attDeleteID': '',
                        'attDeleteType': '',
                        'name': payload.name,
                        'comment': '',
                        'imgFolderID': window.opentext.ImagesFolderOfZBlog,
                        '_journal_blogContent': payload.content,
                        '_journal_blogPublish': 'on',
                        'InheritRequired': 0,
                        'CREATE_Required': 1,
                        'CREATE_Edited': 0,
                        'CREATE_CacheID': 0,
                        'CREATE_CatNames': '',
                        'CTT_ID': '-' + window.opentext.hrBlog,
                        'CTT_PathSaved': 'Z',
                        'ATT_ID': 0,
                        'ATT_PathSaved': '',
                        'ATT_Path': '',
                        'URL_NAME': '',
                        'URL_URL': 'http%3A%2F%2F',
                        'nextStepID': '-1'
            };

            await Vue.prototype.$api.general.post(details);
        }
    }
}