<template>
    <div :class="[showProgram ? 'show-program' : 'show-loader show-program']">
      <v-sheet v-if="showProgram" color="#eee" class="pos-sticky">
        <v-list color="#eee" class="pos-sticky">
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="home"
                :size="36"
                class="v-hover v-grey"
                @click="setType('Home')"
              >
                <v-icon>mdi-home</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="calendar"
                :size="36"
                class="v-hover"
                @click="setType('Calendar')"
              >
                <v-icon>mdi-calendar</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="chat"
                :size="36"
                class="v-hover"
                @click="setType('Chat')"
              >
                <v-icon>mdi-chat-outline</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <!-- <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="hr"
                :size="36"
                class="v-hover"
                @click="setType('HR')"
              >
                <v-icon>mdi-account-tie</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="ceo"
                :size="36"
                class="v-hover"
                @click="setType('CEO')"
              >
                <v-icon>mdi-account-star</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item> -->
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="groups"
                :size="36"
                class="v-hover"
                @click="setType('Groups')"
              >
                <v-icon>mdi-account-group</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
            <v-list-item>
            <v-list-item-content>
              <v-avatar
                id="settings"
                :size="36"
                class="v-hover"
                @click="setType('Settings')"
              >
                <v-icon>mdi-cog</v-icon>
              </v-avatar>
            </v-list-item-content>
            </v-list-item>
        </v-list>
      </v-sheet>
      <v-container v-if="showProgram" fluid class="fix-container">
        <v-row v-if="!onlyMiddle">
          <v-col
            cols="2"
          >
          <div class="pos-sticky">
            <v-sheet
              rounded="lg"
              color="bg-color"
            >
            <v-row>
                <v-col cols="3">
                    <v-avatar
                      class="mt-2"
                      color="primary"
                      size="40"
                      tile
                    >
                      <span class="initials" v-if="!user.photo">{{initials}}</span>
                      <img :src="user.photo" v-if="user.photo">
                    </v-avatar>
                </v-col>
                <v-col class="mt-2">
                    <h3>{{user.firstName}} {{user.lastName}}</h3>
                    <p><a href="#" class="edit-link" @click="viewProfile()">View Profile</a></p>
                </v-col>
            </v-row>
            <v-divider></v-divider>
            <v-list
              dense
              class="w-menu"
              v-model="selectedItem"
            >
              <v-list-item-group mandatory>
                  <v-list-item 
                      v-for="(item, i) in items"
                      :key="i"
                      @click="goToPage(item)"
                  >
                    <v-list-item-content>
                      <v-list-item-title>{{item}}</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
              </v-list-item-group>
            </v-list>
            </v-sheet>
          </div>
          </v-col>

          <v-col
            cols="7"
          >
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <router-view :key="routeKey"></router-view>
            </v-sheet>
          </v-col>

          <v-col
            cols="3"
          >
            <div class="pos-sticky">
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
               <v-card class="w-secondary" v-if="lastAnnouncement">
                 <v-card-title class="head">
                  <span> <v-icon>mdi-bullhorn</v-icon> Announcement </span>
                  </v-card-title>
                  <v-divider></v-divider>
                  <v-card-text>
                    {{lastAnnouncement.NAME}}
                  </v-card-text>
                      <v-card-actions>
                  <v-btn text @click="announcementDialog = true">Open</v-btn>
                  <v-dialog
                     v-model="announcementDialog"
                     fullscreen
                     hide-overlay
                     transition="dialog-bottom-transition"
                   >
                     <template v-slot:activator="{ on, attrs }">
                         <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
                     </template>
                      <v-card>
                         <v-toolbar
                           dark
                           color="primary"
                         >
                           <v-btn
                             icon
                             dark
                             @click="announcementDialog = false"
                           >
                             <v-icon>mdi-close</v-icon>
                           </v-btn>
                           <v-toolbar-title>Announcement</v-toolbar-title>
                         </v-toolbar>  
                         <v-card-text><div v-html="announcementContent"></div></v-card-text>                    
                       </v-card>
                   </v-dialog>
                </v-card-actions>
              </v-card>
              <v-card class="mt-2 w-secondary" v-if="lastCeoMessage">
                <v-card-title class="head">
                  <!-- <v-icon>mdi-account-star</v-icon> -->
                  <span class="name">CEO</span>
                  <v-spacer></v-spacer>
                  <v-btn v-if="isCeoUser" icon @click="deleteCeoMessage()"><v-icon>mdi-delete</v-icon></v-btn>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                   {{lastCeoMessageTitle}}
                </v-card-text>
                <v-card-actions>
                  <v-btn text @click="ceoDialog = true">Open</v-btn>
                  <v-dialog
                     v-model="ceoDialog"
                     fullscreen
                     hide-overlay
                     transition="dialog-bottom-transition"
                   >
                     <template v-slot:activator="{ on, attrs }">
                         <v-btn style="visibility: hidden;" v-bind="attrs" v-on="on"></v-btn>
                     </template>
                         <v-toolbar
                           class="title"
                         >
                           <v-btn
                             icon
                             @click="ceoDialog = false"
                           >
                             <v-icon>mdi-close</v-icon>
                           </v-btn>
                           <v-toolbar-title>{{lastCeoMessageTitle}}</v-toolbar-title>
                         </v-toolbar> 
                         <v-card color="#f7f8f8">
                         <v-card-text class="ceo-message">
                           <v-container fluid>
                            <div v-html="ceoContent"></div>
                           </v-container>
                          </v-card-text>  
                         </v-card>  
                   </v-dialog>
                </v-card-actions>
              </v-card>
              <v-card class="mt-2 w-secondary" v-if="!lastCeoMessage && isCeoUser">
                <v-card-title class="head">
                  <!-- <v-icon>mdi-account-star</v-icon> --> <span class="name">CEO</span>
                </v-card-title>
                 <v-divider></v-divider>
                 <v-card-text>
                   <div style="display:flex; justify-content: center;">
                      <v-dialog
                          @click:outside="onClickOutside"
                          v-model="dialog"
                          persistent
                          max-width="600px"
                      >
                          <template v-slot:activator = "{ on }">
                              <v-btn color="primary" v-on="on">Post new message</v-btn>
                          </template>
                          <v-card>
                              <v-card-title>
                                  <v-container fluid>
                                      <v-row>
                                          <v-col></v-col>
                                          <v-col class="d-flex justify-center"><span>Ceo</span></v-col>
                                          <v-col>
                                              <v-btn 
                                                icon
                                                class="float-right"
                                                @click="close"
                                               >
                                                <v-icon>mdi-close</v-icon>
                                              </v-btn>
                                          </v-col>
                                      </v-row>
                                  </v-container>      
                              </v-card-title>
                              <v-divider class="separator-fix"></v-divider>
                              <v-card-text>
                                 <v-container fluid>
                                     <v-text-field label="Title" @input="activatePost" v-model="ceoMessageTitle"></v-text-field>
                                     <p>Content</p>
                                     <v-textarea
                                        v-model="post"
                                        counter                                     
                                        flat
                                        solo
                                        rows="5"                        
                                        label=""
                                        :rules="rules"
                                        @input="activatePost"
                                      ></v-textarea>
                                 </v-container>
                              </v-card-text>
                              <v-card-actions>
                                <v-container>
                                      <!-- <v-row>
                                          <v-col cols="6">
                                          </v-col>
                                          <v-col cols="6">
                                              <v-btn block text :loading="isSelecting" @click="onAttach">
                                                  <v-icon>mdi-cloud-upload-outline</v-icon>
                                                  <span class="fix-overflow ml-1">{{attachDocButton}}</span>
                                              </v-btn>
                                              <input
                                                ref="uploader"
                                                class="d-none"
                                                type="file"
                                                @change="onFileChanged"
                                              >
                                          </v-col>
                                      </v-row> -->
                                      <v-row>
                                          <v-col>
                                              <v-btn block color="primary" :disabled="isPostDisabled" @click="createNewPost">Post</v-btn>
                                          </v-col>
                                      </v-row>
                                </v-container>
                              </v-card-actions>
                          </v-card>
                      </v-dialog>
                   </div>
                 </v-card-text>
              </v-card>
              <v-card class="mt-2 w-secondary">
                <v-card-title class="head"><v-icon>mdi-poll</v-icon> <span class="name">POLL</span></v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <vue-poll v-bind="pollQuestions" @addvote="addVote"/>
                </v-card-text>
              </v-card>
            </v-sheet>
            </div>
          </v-col>
        </v-row>
        <v-row v-if="onlyMiddle">
          <v-col cols="1"></v-col>
          <v-col cols="10">
            <v-sheet
              rounded="lg"
              class="bg-color"
            >
              <router-view></router-view>
            </v-sheet>
          </v-col>
          <v-col cols="1"></v-col>
        </v-row>
      </v-container>
      <div v-if="!showProgram" class="loader"></div>
    </div>
</template>

<script>
 import { mapActions, mapGetters } from 'vuex';
 import VuePoll from 'vue-poll';
 import moment from 'moment';
 export default {
   name: 'LayoutDefault',
   components: { VuePoll },
   data: () => ({ 
     isCeoUser: window.opentext.isCEOUser,
     dialog: false,
     ceoMessageTitle: '',
     post: '',
     rules: [v => v.length <= 1000 || 'Max 1000 characters'],
     isPostDisabled: true,
     selectedFile: null,
     isSelecting: false,
     defaultButtonText: 'ATTACH DOCUMENT',
     showProgram: false,
     routeKey: '',
     items: ['News Feed', 'Instructions'],
     selectedItem: 0,
     isClicked: false,
     onlyMiddle: false,
     type: 'Home',
     lastCeoMessage: null,
     lastAnnouncement: null,
     ceoContent: "",
     announcementContent: "",
     ceoDialog: false,
     announcementDialog: false,
     pollQuestions: {
        question: 'How long have you been using our product/service?',
        answers: [
            { value: 1, text: 'Less than a month', votes: 1 },
            { value: 2, text: '1 to 6 months', votes: 0 },
            { value: 3, text: '6 months to 1 year', votes: 0 }
        ],
        showResults: false
     },
     ceoImageUrl: null
   }),
   watch: {
     type: function(val){
       switch (val) {
         case 'Home':
           this.onlyMiddle = false;
           this.goTo('newsFeed');
           break;
         case 'Chat':
           this.onlyMiddle = true;
           this.goTo('chat');
           break;
          case 'Settings':
           this.onlyMiddle = true;
           this.goTo('admin');
            break;
          case 'Groups':
            this.onlyMiddle = true;
            this.goTo('groups');
            break;
          case 'Calendar':
            this.onlyMiddle = true;
            this.goTo('calendar');
            break;
          case 'CEO':
            this.onlyMiddle = true;
            this.goTo('ceo');
            break;
          case 'HR':
            this.onlyMiddle = true;
            this.goTo('hr');
            break;
         default:
           break;
       }
     },
     ceoMessages: function(val){
       this.lastCeoMessage = val.find(x=>{
                console.log(new Date(x.DATEEFFECTIVE).getTime());
                var effective_date = new Date(x.DATEEFFECTIVE).getTime();
                var expiration_date = new Date(x.DATEEXPIRATION).getTime();
                var current_date = new Date().getTime();
                if(current_date > effective_date && current_date < expiration_date){
                    return true;
                }
        });
        this.ceoContent = JSON.parse(this.lastCeoMessage.EXTENDEDDATA).Story;
     },
     announcements: function(val){
       console.log(val);
       this.lastAnnouncement = val.find(x=>{
                console.log(new Date(x.DATEEFFECTIVE).getTime());
                var effective_date = new Date(x.DATEEFFECTIVE).getTime();
                var expiration_date = new Date(x.DATEEXPIRATION).getTime();
                var current_date = new Date().getTime();
                if(current_date > effective_date && current_date < expiration_date){
                    return true;
                }
        });
        if(this.lastAnnouncement)
          this.announcementContent = JSON.parse(this.lastAnnouncement.EXTENDEDDATA).Story;
     }
   },
   computed: {
     ...mapGetters('profile', [
       'user',
       'initials'
     ]),
     ...mapGetters('admin', ['menuItems', 'ceoMessages', 'announcements', 'pollData', 'pollResult']),
     lastCeoMessageTitle(){
      return this.lastCeoMessage ? this.lastCeoMessage.NAME : ''
     },
      attachDocButton() {
        return this.selectedFile ? this.selectedFile.name : this.defaultButtonText
     }
   },
   methods: {
     ...mapActions(['initialLoad', 'getApiData']),
     ...mapActions('profile', [
       'getMemberData',
       'getMemberPhoto'
     ]),
     ...mapActions('admin', [
       'getPollData',
       'createNews',
       'deleteNews',
       'addPollAnswer'
     ]),
    ...mapActions('hr', ['getVacancyHealines']),
     setType: function(t){
      this.type = t;
      for (const el of document.getElementsByClassName('v-hover')) {
          el.classList.remove('v-grey');
      }
      var idName = t.toLowerCase();
      document.getElementById(idName).classList.add('v-grey');
     }, 
     viewProfile(){
       this.goTo('profile');
     },
     goToPage(item){
       switch (item) {
          case 'News Feed':
           this.goTo('newsFeed');
           break;
          case 'Instructions':
           this.goTo('instructions');
           break;
          case 'HR':
            this.goTo('hr');
            break;
          case 'Report':
            this.goTo('report');
            break;
          case 'CEO':
            this.goTo('ceo');
            break;
         default:
           let id = this.menuItems.find(x=>x.NAME == item).DATAID;
           this.goTo('/common',{ id: id, name: item})
           break;
       }
     },
     goTo(routeUrl, params){
        this.routeKey = routeUrl;
        if(params){
          this.routeKey = `${routeUrl}/${params.id}/${params.name}`;
          this.$router.push({ path: `${routeUrl}/${params.id}/${params.name}` });
        }else{
          this.$router.push({path: '/' + routeUrl});
        }
     },
     addVote: async function(obj){
       await this.addPollAnswer(obj.value);
     },
     close: function(){
          this.ceoMessageTitle = '';
          this.post = '';
          this.dialog = false;
          this.selectedFile = null;
      },
      activatePost: function(){
            if(this.post.length > 0 && this.ceoMessageTitle.length > 0) this.isPostDisabled = false;
            else this.isPostDisabled = true;
        },
        onClickOutside: function(){
            this.close();
        },
        onAttach: function(){
            this.isSelecting = true;
            this.$refs.uploader.click()
        },
        onFileChanged: function(e){
            this.selectedFile = e.target.files[0];
            console.log(this.selectedFile);
            this.isSelecting = false;
        },
         createNewPost: async function(){ 
            const fiveDaysLater = new Date(new Date().getTime()+(window.opentext.numberOfDaysForCEOMessage*24*60*60*1000));
            const efDate = moment(moment(new Date()).toDate()).format(`YYYY-MM-DDT00:00:00`);
            const expDate = moment(moment(fiveDaysLater).toDate()).format(`YYYY-MM-DDT00:00:00`);
            await this.createNews({
              channelId: window.opentext.ceoMessages,
              name: this.ceoMessageTitle,
              effective_date: efDate,
              expiration_date: expDate,
              story: this.post
            });
             await this.getApiData({
               prefix: 'admin',
               name: 'ceoMessages',
               webreportId: window.opentext.webreportIds.newsId,
               params: '&parentId='+ window.opentext.ceoMessages
             });
            this.close();
        },
        deleteCeoMessage: async function(){
          await this.deleteNews({ID: this.lastCeoMessage.DATAID});
          await this.getApiData({
               prefix: 'admin',
               name: 'ceoMessages',
               webreportId: window.opentext.webreportIds.newsId,
               params: '&parentId='+ window.opentext.ceoMessages
          });
        }
   },
   created: async function(){
     let that = this;
     document.addEventListener('keydown', function(event){
     	if(event.key === "Escape"){
     		if(that.dialog){
                 that.close();
             }
     	}
     });
     await this.getMemberData();
     await this.getMemberPhoto();
     //await this.getPollData();
     await this.initialLoad();
     const menus = this.menuItems.map(x=>x.NAME);
     this.items = ['News Feed', ...menus, 'Instructions']
     this.goTo('newsFeed');
    //  try{ 
    //    const imageId = window.opentext.ceoPhoto;
    //    if(imageId){
    //       const config = {
    //         headers: { 'OTCSTicket': window.opentext.OTCSTicket }
    //       }
    //       let url = `${window.opentext.mainurl}/api/v1/nodes/${imageId}/content`;
    //       const response = await fetch(url, config);
    //       const image = await response.blob();
 
    //       this.ceoImageUrl = window.URL.createObjectURL(image);
    //    }
    //  }catch(e){}
     const IhaveVoted = this.pollResult.find(x=>x.USERID == window.opentext.userId);
     if(IhaveVoted) this.pollQuestions.showResults = true;
        this.pollQuestions.question = JSON.parse(this.pollData[0].EXTENDEDDATA)[0].Question;
        const choices = JSON.parse(this.pollData[0].EXTENDEDDATA)[0].Choices;
        let data = this.pollResult[0].POLLDATA;
        data = data.replace(/{/g, '');
        data = data.replace(/}/g, '');
        data = data.split(',');
        this.pollQuestions.answers = [];
        for (let i = 0; i < data.length; i++) {
          this.pollQuestions.answers.push({ value: i+1, text: choices[i], votes: data[i] });
        } 
     this.showProgram = true;
   },
 }
</script>

<style lang="scss">
    .name{
      margin-left: 10px;
    }
    .title{
      background: linear-gradient(0deg, rgba(194,219,231,1) 40%, rgba(220,236,248,1) 90%)
    }
    .head{
      background-color: #eee;
    }
    .fix-container{
      margin-left: 10px;
      margin-right: 10px;
    }
    .bg-color{
        background-color: #f7f8f8 !important;
    }
    .initials {
        color: white
    }
    .edit-link{
        text-decoration: unset;
    }
    .pos-sticky{
      position: sticky;
      top: 10px;
      height: 100vh;
    }
    .w-menu{
      background-color: unset !important;;
    }
    #v-nav{
      position: sticky;
    }
    .v-hover:hover{
      background-color: grey;
      cursor: pointer;
    }
    .v-grey{
      background-color: grey !important;;
    }
    .w-secondary{
      background-color: #f7f8f8!important;
    }
    .ceo-message{
      color: black !important;
    }
    .show-program{
      display: flex;
    }
    .show-loader{
        height: 100%;
        justify-content: center; 
        align-items: center;
    }
</style>