<template>
  <v-data-table
    :headers="headers"
    :items="сeoMessageUIItems"
    class="elevation-1 bgColor"
  >
    <template v-slot:top>
      <v-toolbar
        flat
        class="title"
      >
        <v-toolbar-title>{{title}}</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
            v-model="dialog"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
          >
          <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" outlined class="float-right" @click="open()">
               New Message
              </v-btn>
          </template>
            <v-toolbar
              class="title">
              <v-btn
                 icon
                 @click="close()"
               >
                 <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>{{title}}</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn outlined @click="save()">Save</v-btn>
            </v-toolbar>
            <v-card color="#f7f8f8">
              <v-card-text>
                <the-news ref="addCeoMessage" :date="date" :date2="date2" :name="name" :story="story" :channel="channelId" :isDisabled="hasDisabled" @close="close()"></the-news>
              </v-card-text>
            </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.story="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="viewMessage(item.id)"
      >
        mdi-eye
      </v-icon>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'
  import TheNews from '../components/TheNews';
  import moment from 'moment'
  export default {
    components: { TheNews },
    data: () => ({
      channelId: window.opentext.ceoMessages,
      title: 'CEO Message',
      dialog: false,
      hasDisabled: false,
      name: '',
      story: '',
      date: new Date().toISOString().substr(0, 10),
      date2: new Date().toISOString().substr(0, 10),
      headers: [
        { text: 'Id', value: 'id' },
        {
          text: 'Name',
          align: 'start',
          value: 'name',
          sortable: false
        },
        { text: 'Start Date', value: 'startDate', sortable: false },
        { text: 'End Date', value: 'endDate', sortable: false },
        { text: 'Story', value: 'story', sortable: false },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      сeoMessageUIItems: [],
    }),

    computed: {
      ...mapGetters('admin', [
          'ceoMessages'
      ]),
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      let that = this;
      document.addEventListener('keydown', function(event){
      	if(event.key === "Escape"){
      		if(that.dialog){
                  that.close();
              }
      	}
      });
      this.initialize()
    },

    methods: {
       ...mapActions(['getApiData']),
       ...mapActions('admin', [
          'deleteNews'
        ]),
        clear: function(){
          this.name = '';
          this.story = '';
          this.date = new Date().toISOString().substr(0, 10);
          this.date2 = new Date().toISOString().substr(0, 10);
        },
        open: function(){
            this.clear();
            this.hasDisabled = false;
            this.dialog = true;
        },
        viewMessage: async function(id){
          var msg = this.ceoMessages.find(x=>x.DATAID == id);
          this.name = msg.NAME;
          this.story = JSON.parse(msg.EXTENDEDDATA).Story;
          this.date = msg.DATEEFFECTIVE;
          this.date2 = msg.DATEEXPIRATION;
          this.hasDisabled = true;
          this.dialog = true;
        },
        deleteMessage: async function(id){
          await this.deleteNews({ID: id});
          await this.getApiData({
              prefix: 'admin',
              name: 'ceoMessages',
              webreportId: window.opentext.webreportIds.newsId,
              params: '&parentId='+ window.opentext.ceoMessages
          });
        },
        close: async function(){
            try{
              await this.getApiData({
                  prefix: 'admin',
                  name: 'ceoMessages',
                  webreportId: window.opentext.webreportIds.newsId,
                  params: '&parentId='+ window.opentext.ceoMessages
              });
              await this.initialize();
            }
            catch(err){
              this.ceoMessages = [];
            }
            this.dialog = false;
            this.hasDisabled = true;
        },  
        initialize () {
          this.сeoMessageUIItems = [];
          this.ceoMessages.map(x=>{
              this.сeoMessageUIItems.push({
                  id: x.DATAID,
                  name: x.NAME,
                  startDate: moment(x.DATEEFFECTIVE).format('DD/MM/YYYY'),
                  endDate: moment(x.DATEEXPIRATION).format('DD/MM/YYYY')
              })
          });
        },
        save: async function(){
          await this.$refs.addCeoMessage.add();
        }
    },
  }
</script>

<style lang="scss" scoped>
  .title{
    background: linear-gradient(0deg, rgba(194,219,231,1) 40%, rgba(220,236,248,1) 90%)
  }
  .w-border{
    border: 0px solid black;
    background-color: #eee;
  }
  .bgColor{
    background-color: #f7f8f8;
  }
</style>