<template>
<v-app class="app-main">
  <v-main class="bg-color parent">
    <layout></layout>
  </v-main>
</v-app>
</template>

<script>
import Layout from './layouts/Layout.vue';
/**
 * @vue-data {String} mainStyles - main style of the program
 */
export default {
  name: 'App',
  components:{
    Layout
  },
  data: () => ({
    appNavColor: 'white',
    mainStyles: 'grey lighten-3'
  }),
  methods:{
    changeTheme: function(){
          this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
          if(this.$vuetify.theme.dark){
            this.appNavColor = 'dark';
            this.mainStyles = '';
          }else{
            this.appNavColor = 'white';
            this.mainStyles = 'grey lighten-3'
          }
    },
    exitApp: function(){
      window.location.href = opentext.mainurl;
    }
  }
};
</script>

<style lang="scss">
  .program-name{
    font-weight: bold;
  }
  .app-main{
     font-family: Tahoma, Verdana, Geneva, Arial, Helvetica, sans-serif !important;
     font-size: 11px !important;
  }
  .w-primary{
    background-color: #eee;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
  .loader {
        border: 16px solid #f3f3f3;
        border-radius: 50%;
        border-top: 16px solid #3498db;
        width: 120px;
        height: 120px;
        -webkit-animation: spin 2s linear infinite; /* Safari */
        animation: spin 2s linear infinite;
    }

    /* Safari */
    @-webkit-keyframes spin {
        0% { -webkit-transform: rotate(0deg); }
        100% { -webkit-transform: rotate(360deg); }
    }

    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }
</style>
