<template>
      <v-sheet
        rounded="lg"
        min-height="85vh"
        class="w-primary"
      >
        <v-toolbar flat class="title">
           <v-toolbar-title>Admin</v-toolbar-title>
           <v-divider
            class="mx-4"
            inset
            vertical
           ></v-divider>
           <v-spacer></v-spacer>
           <v-btn outlined @click="open()">New</v-btn>
        </v-toolbar>
        <v-tabs
            background-color="#eee" 
            color="black"
            v-model="adminTab" 
            vertical
        >
            <v-tab v-for="content in contents" :key="content" @click="getContent(content)">
              {{content}}
            </v-tab>
            <v-tabs-items class="w-primary" v-model="adminTab">
              <v-tab-item>
                <the-admin-instruction></the-admin-instruction>
              </v-tab-item>
              <v-tab-item>
                <the-admin-video-instruction></the-admin-video-instruction>
              </v-tab-item>
              <v-tab-item>
                <the-announcement></the-announcement>
              </v-tab-item>
              <v-tab-item>
                <the-poll></the-poll>
              </v-tab-item>
              <v-tab-item>
                <the-general-news></the-general-news>
              </v-tab-item>
            </v-tabs-items>
          </v-tabs> 
          <div style="display: flex; justify-content: center; margin-top: 20px;">
          <h1>Under construction <v-icon>mdi-account-hard-hat</v-icon></h1>    
          </div>     
      </v-sheet>
</template>

<script>
import { mapActions } from 'vuex';
import TheAdminInstruction from '../components/TheAdminInstruction.vue';
import TheAdminVideoInstruction from '../components/TheAdminVideoInstruction.vue';
import TheGeneralNews from '../components/TheGeneralNews.vue';
import ThePoll from '../components/ThePoll.vue';
import TheAnnouncement from '../components/TheAnnouncement.vue';
 export default {
   components: { TheAnnouncement, TheAdminInstruction, TheAdminVideoInstruction, ThePoll, TheGeneralNews },
   data: () => ({
    adminTab: null,
    num: 1,
    contents: ['Instructions', 'Video Instructions' , 'Announcemets', 'POLL', 'Innovations']
  }),
   created: async function() {
     this.getApiData({
        prefix: 'admin',
        name: 'polls',
        webreportId: opentext.webreportIds.childrenElementsId,
        params: `&parentid=${opentext.cicCommunityPoll}`
     });
   },
   methods: {
     ...mapActions([
       'getApiData'
     ]),
     getContent: function(content){
       switch (content) {
         case 'Elanlar':
           this.num = 1;
           break;
         case 'Təlimatlar':
           this.num = 2;
           break;
         case 'Video Təlimatlar':
           this.num = 3;
           break;
         case 'Sorğular':
           this.num = 4;
           break;
          case 'Yeniliklər':
            this.num = 5;
            break;
         default:
           break;
       }
     }
   }
 }
</script>

<style lang="scss" scoped>
  .title{
    background: linear-gradient(0deg, rgba(194,219,231,1) 40%, rgba(220,236,248,1) 90%)
  }
  .w-primary{
    background-color: #eee;
  }
  .w-secondary{
    background-color: #f7f8f8;
  }
</style>
