<script>
import { mapGetters, mapActions } from 'vuex';
import TheNews from '../components/TheNews';
export default {
    name: 'TheAnnouncement',
    components: { TheNews },
    data: () => ({
      channelId: window.opentext.announcements,
      dialog: false,
      hasDisabled: false,
      name: '',
      story: '',
      date: new Date().toISOString().substr(0, 10),
      date2: new Date().toISOString().substr(0, 10),
      headers: [
        { text: 'Id', value: 'id' },
        {
          text: 'Name',
          align: 'start',
          value: 'name',
          sortable: false
        },
        { text: 'Start Date', value: 'startDate', sortable: false },
        { text: 'End Date', value: 'endDate', sortable: false },
        { text: 'Actions', value: 'actions', sortable: false }
      ],
      items: []
    }),
    computed: {
      ...mapGetters('admin', [
            'announcements'
        ])
    },
    created: function(){
        let that = this;
        document.addEventListener('keydown', function(event){
        	if(event.key === "Escape"){
        		if(that.dialog){
                    that.close();
                }
        	}
        });
    },
    methods: {
        ...mapActions(['getApiData']),
        ...mapActions('admin', [
          'deleteNews'
        ]),
        clear: function(){
          this.name = '';
          this.story = '';
          this.date = new Date().toISOString().substr(0, 10);
          this.date2 = new Date().toISOString().substr(0, 10);
        },
        open: function(){
            this.clear();
            this.hasDisabled = false;
            this.dialog = true;
        },
        viewMessage: async function(id){
          var msg = this.announcements.find(x=>x.DATAID == id);
          this.name = msg.NAME;
          this.story = JSON.parse(msg.EXTENDEDDATA).Story;
          this.date = msg.DATEEFFECTIVE;
          this.date2 = msg.DATEEXPIRATION;
          this.hasDisabled = true;
          this.dialog = true;
        },
        deleteMessage: async function(id){
          await this.deleteNews({ID: id});
          await this.getApiData({
              prefix: 'admin',
              name: 'announcements',
              webreportId: window.opentext.webreportIds.newsId,
              params: '&parentId='+ window.opentext.announcements
          });
        },
        close: async function(){
           await new Promise(r => setTimeout(r, 2000));
           await this.getApiData({
              prefix: 'admin',
              name: 'announcements',
              webreportId: window.opentext.webreportIds.newsId,
              params: '&parentId='+ window.opentext.announcements
          });
            this.dialog = false;
            this.hasDisabled = true;
        },
    }
}
</script>

<style lang="scss" scoped>
  .title{
