<template>
    <div>
      <v-card>
        <v-card-text>
           <v-simple-table 
            fixed-header 
            dense 
            height="300">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Name
                  </th>
                  <th class="text-left">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody>
                 <tr
                  v-for="item in generalNews"
                  :key="item.DATAID"
                >
                  <td>{{ item.NAME }}</td>
                  <td>
                      <v-btn icon @click="viewMessage(item.DATAID)">
                          <v-icon>mdi-eye</v-icon>
                      </v-btn>
                      <v-btn icon @click="deleteMessage(item.DATAID)">
                          <v-icon>mdi-trash-can-outline</v-icon>
                      </v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
          >
          <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" text class="float-right" @click="open()">
               <v-icon>mdi-plus</v-icon>Yeni
              </v-btn>
          </template>
            <v-toolbar
                dark
                color="primary">
              <v-spacer></v-spacer>
              <v-btn
                 icon
                 @click="close()"
               >
                 <v-icon>mdi-close</v-icon>
               </v-btn>
            </v-toolbar>
            <v-card>
              <v-card-text>
                <the-news :date="date" :date2="date2" :name="name" :story="story" :channel="889054" :isDisabled="hasDisabled" @close="close()"></the-news>
              </v-card-text>
            </v-card>
          </v-dialog>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import TheNews from '../components/TheNews';
export default {
    name: 'TheGeneralNews',
    components: { TheNews },
    data: () => ({
        dialog: false,
        hasDisabled: false,
        name: '',
        story: '',
        date: new Date().toISOString().substr(0, 10),
        date2: new Date().toISOString().substr(0, 10)
    }),
    computed: {
      ...mapGetters('admin', [
            'generalNews'
        ]),
    },
    created: function(){
        let that = this;
        document.addEventListener('keydown', function(event){
        	if(event.key === "Escape"){
        		if(that.dialog){
                    that.close();
                }
        	}
        });
    },
    methods: {
        ...mapActions(['getApiData']),
        ...mapActions('admin', [
          'deleteNews'
        ]),
        clear: function(){
          this.name = '';
          this.story = '';
          this.date = new Date().toISOString().substr(0, 10);
          this.date2 = new Date().toISOString().substr(0, 10);
        },
        open: function(){
            this.clear();
            this.hasDisabled = false;
            this.dialog = true;
        },
        viewMessage: async function(id){
          var msg = this.generalNews.find(x=>x.DATAID == id);
          this.name = msg.NAME;
          this.story = JSON.parse(msg.EXTENDEDDATA).Story;
          this.date = msg.DATEEFFECTIVE;
          this.date2 = msg.DATEEXPIRATION;
          this.hasDisabled = true;
          this.dialog = true;
        },
        deleteMessage: async function(id){
          await this.deleteNews({ID: id});
          await this.getApiData({
              prefix: 'admin',
              name: 'generalNews',
              webreportId: window.opentext.webreportIds.newsId,
              params: '&parentId='+ window.opentext.generalNews
          });
        },
        close: async function(){
            try{await this.getApiData({
              prefix: 'admin',
              name: 'generalNews',
              webreportId: window.opentext.webreportIds.newsId,
              params: '&parentId='+ window.opentext.generalNews
            });}catch(err){
              this.generalNews = [];
            }
            this.dialog = false;
            this.hasDisabled = true;
        },
    }
}
</script>

<style lang="scss" scoped>
  .dialog-name{
    font-size: 0.8em;
  }
  .v-error{
    color: red;
  }
</style>