import Vue from 'vue'
import Vuex from 'vuex'
import profile from './modules/profile.js'
import newsFeed from './modules/newsFeed.js'
import admin from './modules/admin.js'
import hr from './modules/hr.js'
import calendar from './modules/calendar.js'
import member from './modules/member.js'
import chat from './modules/chat.js'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: () => ({
    isLoaded: false
  }),
  getters: {
    isLoaded: (state) => state.isLoaded
  },
  mutations: {
    dataLoaded: (state) => {
      state.isLoaded = true;
    },
    setApiData: (state, payload) => {
      state[payload.prefix][payload.name] = _.cloneDeep(payload.data);
    }
  },
  actions:{
    getApiData: async (context, payload) => {
      try{
          if(!payload) throw new Vue.prototype.BaseError(payload.name, "Payload is not provided");        
          const data = await Vue.prototype.$api.general.get(payload.webreportId, payload.params);
          context.commit('setApiData', {prefix: payload.prefix, name: payload.name, data: data});
      }catch(err){
          console.log(`${err.error_function}: ${err.message}`);
      }
    },
    initialLoad: async (context) => {
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'menuItems',
        webreportId: window.opentext.webreportIds.childrenId,
        params: `&parentId=${window.opentext.menuFolder}&subType=${window.opentext.subTypes.channel}`
      });
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'instructions',
        webreportId: window.opentext.webreportIds.childrenId,
        params: `&parentId=${window.opentext.instructionId}&subType=${window.opentext.subTypes.wiki}`
      });
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'videoInstructions',
        webreportId: window.opentext.webreportIds.childrenId,
        params: `&parentId=${window.opentext.videoFolder}&subType=${window.opentext.subTypes.document}`
      });
      const d = new Date(new Date().getFullYear(), new Date().getMonth()+1, 0);
      const numberOfDaysInCurrentMonth = d.getDate();

      await context.dispatch('getApiData', { 
        prefix: 'admin',
        name: 'generalNews',
        webreportId: window.opentext.webreportIds.newsId,
        params: `&parentId=${window.opentext.generalNews}`
      }); 
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'ceoMessages',
        webreportId: window.opentext.webreportIds.newsId,
        params: '&parentId='+ window.opentext.ceoMessages
      });
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'announcements',
        webreportId: window.opentext.webreportIds.newsId,
        params: '&parentId='+ window.opentext.announcements
      });
      await context.dispatch('getApiData', {
        prefix: 'hr',
        name: 'blogHeadlines',
        webreportId: window.opentext.webreportIds.blogHeadline,
        params: '&blogId='+ window.opentext.hrBlog
      });
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'innovations',
        webreportId: window.opentext.webreportIds.blogHeadline,
        params: '&blogId='+ window.opentext.innovationBlog
      });
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'pollData',
        webreportId: window.opentext.webreportIds.pollId,
        params: `&inputLabel1=${window.opentext.pollId}`
      }, { root:true }); 
      await context.dispatch('getApiData', {
        prefix: 'admin',
        name: 'pollResult',
        webreportId: window.opentext.webreportIds.pollResultId,
        params: `&pollId=${window.opentext.pollId}`
      }, { root:true }); 
      await context.dispatch('getApiData', {
        prefix: 'hr',
        name: 'vacancies',
        webreportId: window.opentext.webreportIds.newsId,
        params: '&parentId=' + window.window.opentext.hrVacancies
      });
      await context.dispatch('getApiData', {
        prefix: 'hr',
        name: 'news',
        webreportId: window.opentext.webreportIds.newsId,
        params: '&parentId=' + window.window.opentext.hrNews
      });
      await context.dispatch('getApiData', {
        prefix: 'hr',
        name: 'motivationStories',
        webreportId: window.opentext.webreportIds.newsId,
        params: '&parentId=' + window.window.opentext.hrMotivationStories
      });
      context.commit('dataLoaded');
    }
  },
  modules: {
    profile,
    newsFeed,
    admin,
    hr,
    calendar,
    member,
    chat
  }
});

export default store;
